import React, { Component, useState } from 'react';
import {
  FormGroup, ControlLabel, FormControl, Row, Checkbox, Col,
} from 'react-bootstrap';

function FieldGroup({
  label, name, options, handleChange, checkboxes, ...props
}) {
  const [checkboxData, setCheckboxData] = useState({});

  let renderOptions = false;
  if (options)
    renderOptions = ['Select...', ...options];

  if (checkboxes) {
    const onCheckbox = (option) => {
      const formData = { target: { value: !checkboxData[option] } };
      handleChange(formData, option);
      const newData = { ...checkboxData, [option]: !checkboxData[option] };
      setCheckboxData(newData);
      console.log('checkbox', { option, newData });
    };
    return (
      <FormGroup>
        <ControlLabel>{label}</ControlLabel>
        {
          options && options.map((option, i) => (
            <Row
              style={{ paddingLeft: '30px' }}
              onClick={(e) => onCheckbox(option)}
            >
              <input
                onClick={(e) => console.log('ckbox', e.target.value)}
                key={i}
                type="checkbox"
                checked={checkboxData[option]}
              />
              {' '}
              {option}
            </Row>
          ))
        }
      </FormGroup>
    );
  }

  const optionsRender = options
    ? options && renderOptions.map((option, i) => (
      <option key={i} value={option}>{option}</option>
    ))
    : null;

  return (
    <FormGroup>
      <ControlLabel>{label}</ControlLabel>
      <FormControl
        onChange={(e) => handleChange(e, name)}
        {...props}
      >
        {optionsRender}
      </FormControl>
    </FormGroup>
  );
}

export class FormInputs extends Component {
  render() {
    const row = [];
    for (let i = 0; i < this.props.ncols.length; i++)
      row.push(
        <div key={i} className={this.props.ncols[i]}>
          <FieldGroup
            handleChange={this.props.handleChange}
            {...this.props.properties[i]}
          />
        </div>,
      );

    return <Row>{row}</Row>;
  }
}

export default FormInputs;
